.navBar {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	font-size: 15px;
	font-family: Roboto;
}

.navBar > p {
	color: gray;
	font-weight: 700;
	margin-left: 1.6rem;
	cursor: pointer;
}

/* Adapting to screens with max height of 800px */
@media (max-height: 800px) {
	.navBar {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: row;
		font-size: 13px;
		font-family: Roboto;
		margin-top: 3rem;
	}
	
	.navBar > p {
		color: gray;
		font-weight: 700;
		margin-left: 1.6rem;
	}
}

/* Adapting to screens with min width of 700px */
@media (min-width: 700px) {
	.navBar {
		justify-content: flex-start;
		margin-left: 5rem;
		font-size: 20px;
		margin-top: -1rem;
	}
	
	.navBar > p {
		margin-left: 3rem;
	}
}
